<template>
  <v-touch @swiperight='onSwipeRight' @swipeleft='onSwipeLeft'>
    <div class="header">
      <router-link tag='div' to='/city' class="left">
        <span>{{cityName}}</span>
        <img data-v-4070467a="" src="data:image/png;base64," width="6px" height="3px">
      </router-link>
      <p>影院</p>
      <router-link to="/cinemas/search" class="search">
        <img
          src="data:image/png;base64,"
          alt="search"
        />
      </router-link>
    </div>
    <div class="nav">
      <ul>
        <li @click='handleClick("isSelectShow")' :class="isSelectShow?'seleted':''">
          <span>{{currentCity}}</span>
          <img class="show" v-if='!isSelectShow'  src="data:image/png;base64,"></img>
          <img class="noshow" v-else src='data:image/png;base64,'></img>
        </li>
        <li @click='handleClick("isOrderShow")' :class="isOrderShow?'seleted':''">
          <span>{{order}}</span>
          <img class="show" v-if='!isOrderShow' src="data:image/png;base64,"></img>
          <img class="noshow"  v-else src='data:image/png;base64,'></img>
        </li>
        <li @click='handleClick("isRecentShow")' :class="isRecentShow?'seleted':''">
          <span>最近去过</span>
          <img class="show" v-if='!isRecentShow'  src="data:image/png;base64,"></img>
          <img class="noshow" v-else src='data:image/png;base64,'></img>
        </li>
      </ul>
    </div>
    <div class="select" v-if='isSelectShow'>
      <ul>
        <li v-for='data in citylist' :key='data' @click='selectCity(data)'>
          <p  :class="data===currentCity?'active':''">{{data}}</p>
        </li>
      </ul>
    </div>
    <div class="order" v-if='isOrderShow'>
      <ul>
        <li :class="orderflag?'activefont':''"  @click='orderChange(0)'>
          <img src="data:image/png;base64," width="13px" height="9px" alt="" v-if='orderflag'>
           <span class="placehold" style="width: 13px;" v-else></span>
          <span >APP订票</span>
        </li>
        <li :class="!orderflag?'activefont':''"  @click='orderChange(1)'>
           <span class="placehold" style="width: 13px;" v-if='orderflag'></span>
           <img src="data:image/png;base64," width="13px" height="9px" alt="" v-else>
          <span>前台兑换</span>
        </li>
      </ul>
    </div>
     <div class="recent" v-if='isRecentShow'>
      <ul>
        <li>
          <img src="data:image/png;base64," width="13px" height="9px" alt="">
          <span class="activefont">最近去过</span>
        </li>
        <li>
          <span class="placehold" style="width: 13px;"></span>
          <span>离我最近</span>
        </li>
      </ul>
    </div>
    <div class="dang"></div>
    <loading :isshow="isshow"></loading>
    <cinemalist :datalist="selectList" class="selectlist" :orderflag='orderflag' :style="{height:scrollHeight}"></cinemalist>
  </v-touch>
</template>
<script>
// eslint-disable-next-line @typescript-eslint/no-unused-vars
import Vue from 'vue'
import VueTouch from 'vue-touch'
import axios from 'axios'
import http from '@/utils/http'
import loading from '../components/Loading'
import cinemalist from '../components/Cinemalist'
import { mapState, mapMutations } from 'vuex'
import BetterScroll from 'better-scroll'
import { Toast } from 'vant'
Vue.use(VueTouch, { name: 'v-touch' })
Vue.use(Toast)
export default {
  data () {
    return {
      isshow: true,
      currentCity: '全城',
      isSelectShow: false,
      isNavActive: false,
      isOrderShow: false,
      isRecentShow: false,
      scrollHeight: '0px',
      order: 'APP订票',
      orderflag: true
    }
  },
  components: {
    loading,
    cinemalist
  },
  methods: {
    ...mapMutations('cinema', ['SaveCinemalist']),
    handleClick (data) {
      const arr = ['isSelectShow', 'isOrderShow', 'isRecentShow']
      arr.filter(val => val !== data).forEach(val => {
        this[val] = false
      })
      this[data] = !this[data]
    },
    selectCity (data) {
      this.currentCity = data
      this.handleClick()
    },
    onSwipeRight () {
      this.$router.push('/films')
    },
    onSwipeLeft () {
      this.$router.push('/center')
    },
    orderChange (data) {
      if (data) {
        this.order = '前台兑换'
        this.orderflag = false
        http.request({
          url: '/gateway?cityId=110100&ticketFlag=2&k=9241158',
          headers: {
            'X-Host': 'mall.film-ticket.cinema.list'
          }
        }).then(res => {
          this.SaveCinemalist(res.data.data.cinemas)
          Toast.clear()
        })
        Toast.loading({
          message: '加载中...',
          forbidClick: true,
          loadingType: 'spinner'
        })
      } else {
        this.order = 'APP兑换'
        this.orderflag = true
        this.$store.dispatch('cinema/findCinemaList', this.cityId)
      }
      this.isOrderShow = false
    }
  },
  computed: {
    ...mapState('cinema', ['cinemaList']),
    ...mapState('cityN', ['cityId', 'cityName']),
    citylist () {
      return ['全城', ...new Set(this.cinemaList.map(val => val.districtName))]
    },
    selectList () {
      if (this.currentCity === '全城') {
        return this.cinemaList
      } else {
        return this.cinemaList.filter(val => val.districtName === this.currentCity)
      }
    }
  },
  mounted () {
    this.scrollHeight = document.documentElement.clientHeight - 94 - 49 + 'px'
    // eslint-disable-next-line no-new
    new BetterScroll('.selectlist')
    if (this.cinemaList.length === 0) {
      this.isshow = false
      this.$store.dispatch('cinema/findCinemaList', this.cityId)
    }
  }
}
</script>

<style lang="scss" scoped>
* {
  margin: 0;
  padding: 0;
}
.selectlist{overflow: hidden;}
.dang{height: 94px;}
.order,.recent{
   z-index: 101;
  background-color: #fff;
  position: fixed;
  top:94px;
  left: 0;
  width: 100%;
   ul{
    display: flex;
    flex-direction: column;
    .activefont{color: #ff5f16;}
    li{
      border-bottom: 1px solid #ededed;
      padding:0 0 0 25px;
      list-style: none;
      text-align: left;
      span{
        margin-left: 10px;
    font-size: 13px;
    line-height: 44px;}
    }
}
}
.select{
  z-index: 101;
  background-color: #fff;
  position: fixed;
  top:94px;
  left: 0;
  width: 100%;
  ul{
    padding: 10px 0 0;
    margin: 0 0 0 10px;
    display: flex;
    flex-wrap: wrap;
    .active{border: 1px solid #ff5f16;color: #ff5f16;}
    li{
      list-style: none;
      width: 22%;
      height:30px;
      padding-bottom: 15px;
      padding-right: 10px;
      text-align: center;
    p{
    border-radius: 3px;
    border: 1px solid rgba(210, 214, 220, 0.5);
    color: #797d82;
    color: #797d82;
    text-align: center;
    height: 30px;
    line-height: 27px;
    font-size:12px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    }
    }
  }
}
.nav {
  overflow: hidden;
  ul {
    display: flex;
    position: fixed;
    height: 50px;
    width: 100%;
    top: 44px;
    right: 0;
    text-align: center;
    background-color: #fff;
    z-index: 2000;
     .seleted{
      span{
      color: #ff5f16;
      }
       img.noshow {
         visibility: visible;
        display: inline-block;
        width: 6px;
        height: 3px;
        vertical-align: middle;
      }}
    li {
      border-bottom: 1px solid #ededed;
      flex: 1;
      list-style: none;
      span {
        font-size: 13px;
        max-width: 58px;
        line-height: 50px;
        overflow: hidden;
        -o-text-overflow: ellipsis;
        text-overflow: ellipsis;
        white-space: nowrap;
        padding-right: 5px;
        color: #191a1b;
      }
       .show {
        display: inline-block;
        width: 6px;
        height: 3px;
        vertical-align: middle;
      }
       .noshow {
        visibility: hidden;
        display: inline-block;
        width: 6px;
        height: 3px;
        vertical-align: middle;
      }
    }
  }
}
.loading {
  width: 100%;
  margin: 0 auto;
}
.header {
  border-bottom: 1px solid #ededed;
  position: fixed;
  height: 44px;
  left: 0;
  top: 0;
  width: 100%;
  text-align: center;
  font: 17px/44px "";
  color: #191a1b;
  background: #fff;
  display: flex;
  align-items: center;
  z-index: 1111;
  .left{
    position: absolute;
    left: 15px;
    span{font-size: 13px;
    max-width: 58px;
    height: 16px;
    line-height: 16px;
    padding-right: 5px;
    color: #191a1b;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    }
    img{width: 6px;height: 3px;}
  }
  p {
    flex: 3;
  }
  img {
    width: 18px;
    height: 18px;
    vertical-align: middle;
    margin-right: 20px;
  }
}
</style>
